.footer__text {
    font-family: 'Inter', Arial, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #545454;
    margin: 0;
    padding: 0;
}

@media screen and (max-width: 540px) {
    .footer__text {
      margin-left: 19px;
      font-size: 14px;
      line-height: 16.94px;
    }
}